.footer {
    background-color: $color-dark-grey-3;
    padding: 10rem 0;
    font-size: 1.4rem;
    color: $color-light-grey1;

    @include respond(tabport) {
        padding: 8rem 0;
    }
    &__logo-box {
        text-align: center;
        margin-bottom: 8rem;

        @include respond(tabport) {
            margin-bottom: 4rem;
        }
    }
    &__logo {
        width: 15rem;
        height: auto;
    }
    &__navigation {
        border-top: .1rem solid $color-dark-grey-2;
        padding-top: 2rem;
        display: inline-block;

        @include respond(tabport) {
            width: 100%;
            text-align: center;
            padding: 1rem 1rem 0 1rem;
        }
    }
    &__list {
        list-style: none;
    }
    &__item {
        display: inline-block;

        &:not(:last-child) {
            margin-right: 1.5rem;
        }
    }
    &__link {
        &:link, &:visited {
            color: $color-light-grey1;
            background-color: $color-dark-grey-3;
            text-decoration: none;
            text-transform: uppercase;
            display: inline-block;
            transition: all .2s;
        }
        &:hover, &:active {
            color: $color-primary;
            box-shadow: 0 1rem 2rem rgba($color: $color-black, $alpha: .4);
            transform: rotate(5deg) scale(1.3);
        }
    }
    &__copyright {
        border-top: .1rem solid $color-dark-grey-2;
        padding-top: 2rem;
        width: 84%;
        float: right;

        @include respond(tabport) {
            width: 100%;
            float: none;
            padding: 1rem 1rem 0 1rem;
        }
    }
}