.section-about {
    background-color: $color-light-grey1;
    padding: 25rem 0;
    margin-top: -20vh;

    @include respond(tabport) {
        padding: 20rem 0;
    }
}
.section-features {
    margin-top: -16vh;
    padding: 20rem 0;
    background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark), url(../img/nat-4.jpg);
    background-blend-mode: screen;
    background-size: cover;
    transform: skewY(-7deg);

    & > * {
        transform: skewY(7deg);
    }
    @include respond(tabport) {
        padding: 10rem 0;
    }
}
.section-tours {
    background-color: $color-light-grey1;
    padding: 25rem 0 15rem 0;
    margin-top: -10vh;

    @include respond(tabport) {
        padding: 15rem 0 10rem 0;
    }
}
.section-stories {
    padding: 15rem 0;
    position: relative;

    @include respond(tabport) {
        padding: 5rem 0;
    }
}
.section-booking {
    padding: 15rem 0;
    background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);

    @include respond(tabport) {
        padding: 10rem 0;
    }
}
.book {
    background-image: linear-gradient(105deg, rgba($color-white, .9) 0%, rgba($color-white, .9) 50%, transparent 50%),url(../img/nat-10.jpg);
    background-size: cover;
    border-radius: 1rem;
    box-shadow: 0 1.5rem 4rem rgba($color-black, .4);

    @include respond(tabland) {
        background-image: linear-gradient(105deg, rgba($color-white, .9) 0%, rgba($color-white, .9) 65%, transparent 65%),url(../img/nat-10.jpg);
    }
    @include respond(tabport) {
        background-image: linear-gradient(to right, rgba($color-white, .9) 0%, rgba($color-white, .9) 100%),url(../img/nat-10.jpg);
    }
    &__form {
        padding: 6rem;
        width: 50%;

        @include respond(tabland) {
            width: 65%;
        }
        @include respond(tabport) {
            width: 100%;
        }
    }
}