.hdr {
    height: 95vh;
    background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark), url(../img/hero-small.jpg);
    background-blend-mode: darken;
    background-size: cover;
    background-position: top;
    position: relative;
    -webkit-clip-path: polygon(0 0,100% 0,100% 75vh,0 100%);
    clip-path: polygon(0 0,100% 0,100% 75vh,0 100%);

    @include respond(phone) {
        -webkit-clip-path: polygon(0 0,100% 0,100% 85vh,0 100%);
        clip-path: polygon(0 0,100% 0,100% 85vh,0 100%);
    }
    @media only screen and (min-resolution: 192dpi) and (min-width: 37.5em), (min-width: 125em) {
        background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark), url(../img/hero.jpg);
    }
    &__logo-box {
        position: absolute;
        top: 4rem;
        left: 4rem;
    }
    &__logo-box-logo {
        height: 3.5rem;
    }
    &__hdng-box {
        position: absolute;
        top: 40%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
    }
}